<template>
    <div class="webuploader-container">
        <div class="btns">
            <div ref="btn"><slot></slot></div>
        </div>
        <!--用来存放文件信息-->
        <ul class="ivu-upload-list">
            <li class="ivu-upload-list-file">
                <span><Icon type="ios-document-outline"></Icon> 3.psd</span>
                <Icon class="ivu-upload-list-remove" type="ios-close"></Icon>
                <Progress :percent="25" status="active" :stroke-width="4" />
            </li>
        </ul>

    </div>
</template>

<script>
    import Config from '../../config';
    export default {
        name: 'vue-webuploader',
        props: {
            multiple: {
                type: Boolean,
                default: true
            },
            /**
             * 指定接受哪些类型的文件。 由于目前还有ext转mimeType表
             */
            accept: {
                type: Array,
                default: null
            },
            formData: {
                type: Object,
                default() {
                    return {};
                }
            }
        },
        data() {
            return {
                uploader: null,
                // 上传地址
                server: Config[Config.env].origin + Config[Config.env].ajaxUrl + '/file/upload/user_attach',
                swf: Config[Config.env].origin + '/webuploader/Uploader.swf',
            };
        },
        mounted() {
            this.initWebuploader();
            this.initEvent();
        },
        methods: {
            init() {
            },
            initWebuploader() {
                if ( !WebUploader.Uploader.support() ) {
                    alert( 'Web Uploader 不支持您的浏览器！如果你使用的是IE浏览器，请尝试升级 flash 播放器');
                    throw new Error( 'WebUploader does not support the browser you are using.' );
                }
                this.uploader = WebUploader.create({
                    swf: this.swf,
                    server: this.server,
                    pick: {
                        id: this.$refs.btn,
                        multiple: this.multiple
                    },

                    accept: this.accept,
                    thumb: {
                        width: 110,
                        height: 110,
                        // 图片质量，只有type为`image/jpeg`的时候才有效。
                        quality: 70,
                        // 是否允许放大，如果想要生成小图的时候不失真，此选项应该设置为false.
                        allowMagnify: true,
                        // 是否允许裁剪。
                        crop: true,
                        // 为空的话则保留原有图片格式。
                        // 否则强制转换成指定的类型。
                        type: 'image/jpeg'
                    },
                    // 配置压缩的图片的选项。如果此选项为false, 则图片在上传前不进行压缩。
                    compress: false,
                    // {Boolean} [可选] [默认值：false] 设置为 true 后，不需要手动调用上传，有文件选择即开始上传。
                    auto: true,
                    // 开起分片上传。
                    chunked: false,
                    // {Object} [可选] [默认值：{}] 文件上传请求的参数表，每次发送都会发送此对象中的参数。
                    formData: this.formData
                });
            },
            initEvent() {
                // 当文件被加入队列之前触发，此事件的handler返回值为false，则此文件不会被添加进入队列。
                this.uploader.on('beforeFileQueued', this.beforeFileQueued);
                // 当文件被加入队列以后触发。
                this.uploader.on('fileQueued', this.fileQueued);
                // 当一批文件添加进队列以后触发。
                this.uploader.on('filesQueued', this.filesQueued);
                // 当文件被移除队列后触发。
                this.uploader.on('fileDequeued', this.fileDequeued);
                // 当 uploader 被重置的时候触发。
                this.uploader.on('reset', this.reset);
                // 当开始上传流程时触发。
                this.uploader.on('startUpload', this.startUpload);
                // 当开始上传流程暂停时触发
                this.uploader.on('stopUpload', this.stopUpload);
                // 当所有文件上传结束时触发。
                this.uploader.on('uploadFinished', this.uploadFinished);
                // 某个文件开始上传前触发，一个文件只会触发一次。
                this.uploader.on('uploadStart', this.uploadStart);
                // 上传过程中触发，携带上传进度。
                this.uploader.on('uploadProgress', this.uploadProgress);
                // 当文件上传出错时触发。
                this.uploader.on('uploadError', this.uploadError);
                // 当文件上传成功时触发。
                this.uploader.on('uploadSuccess', this.uploadSuccess);
                // 不管成功或者失败，文件上传完成时触发。
                this.uploader.on('uploadComplete', this.uploadComplete);
                //
            },
            beforeFileQueued(file) {
                return true;
            },
            fileQueued(file) {
                console.dir('当文件被加入队列以后触发。');
                console.dir(file);
            },
            filesQueued(files) {
                console.dir('当一批文件添加进队列以后触发');
                console.dir(files);
            },
            fileDequeued(file) {
                console.dir('当文件被移除队列后触发');
            },
            reset(files) {
                console.dir('当 uploader, 被重置的时候触发');
            },
            startUpload() {

            },
            stopUpload() {},
            uploadFinished() {

            },
            uploadStart(file) {
                console.dir('一个文件只会触发一次');
            },
            uploadProgress(file, percentage) {
                console.dir('上传过程中触发，携带上传进度。');
                console.dir(percentage);
            },
            uploadError(file, reason) {
                console.dir('当文件上传出错时触发');
                console.dir(reason);
            },
            uploadSuccess(file, reason) {
                console.dir('当文件上传成功时触发');
                console.dir(file);
            },
            uploadComplete(file) {
                console.dir('不管成功或者失败，文件上传完成时触发。');
            },
        }
    }
</script>

<style lang="scss">
    .webuploader-container {
        .webuploader-container {
            position: relative;
        }
        .webuploader-element-invisible {
            position: absolute !important;
            clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
            clip: rect(1px,1px,1px,1px);
        }
        .webuploader-pick {
            position: relative;
            display: inline-block;
        }
